import rwxNumberSwitchers from 'roseworx/js/components/rwxNumberSwitchers';

document.getElementById('setNumberDemo').addEventListener('click', ()=>{
	const val = document.getElementById('setNumberVal').value;
	[...document.querySelectorAll('[rwx-number-switcher]')].map((el)=>{
		rwxNumberSwitchers.switch(el.id, parseInt(val));
	})
});